import { Component, OnInit } from '@angular/core';
import { ProductService } from '../../services/product/product.service';
import { Router } from '@angular/router';

@Component({
  selector: 'ngx-componentdetails',
  templateUrl: './componentdetails.component.html',
  styleUrls: ['./componentdetails.component.scss']
})
export class ComponentdetailsComponent implements OnInit {
  data:any;
  loading:boolean = true;

  constructor(private productService:ProductService,private route:Router) { }

  ngOnInit() {
    this.getData();

  }

  getData(){
    this.loading = true;
    this.data = {};
    let path = this.route.url.split("/");
    let id = path[path.length - 1];
    this.productService.getMethod(
      (response)=>{
        this.loading = false;
        console.log(response);
        this.data = response;
      },
      (error)=>{
        console.log(error);
      },
      {},
      "dashboard/componentdetails/"+id.toString()
    );
  }

  refresh($event){
    console.log("refresh");
    this.getData();
  }

}
