import { Component, OnInit , Input , Output , EventEmitter } from '@angular/core';
import { FormGroup , FormControl , Validators,  } from '@angular/forms';
import { imageBaseUrl } from '../../../Models/common';
import {  } from '../../../Models/common';
import { ProductService } from '../../../services/product/product.service';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';

// import { EventEmitter } from 'events';

@Component({
  selector: 'ngx-slideradds',
  templateUrl: './slideradds.component.html',
  styleUrls: ['./slideradds.component.scss']
})
export class SlideraddsComponent implements OnInit {

  @Input() data;
  @Output() eventEmitor = new EventEmitter();
  title:string = "";
  formGroup:FormGroup;
  slideGroup:FormGroup;
  submited:boolean = false;
  submitedSlide:boolean = false;
  uploading:boolean = false;
  BASE_URL:string;
  productList:any = [];
  selectedSliderId:any = 0;
  closeResults:string;

  constructor(private productServices:ProductService,private modalService: NgbModal) { }

  ngOnInit() {
    this.BASE_URL = imageBaseUrl;
    console.log(this.data);
    if(this.data.data.length > 0){
      this.formGroup = new FormGroup({
        title: new FormControl(this.data.data[0].title,[Validators.required]),
        slder: new FormControl(this.data.id,[Validators.required])
      });
    }
    else{
      this.formGroup = new FormGroup({
        title: new FormControl('',[Validators.required]),
        slder: new FormControl(this.data.id,[Validators.required])
      });
    }
    this.slideGroup = new FormGroup({
      title:new FormControl('',[Validators.required]),
      file:new FormControl('',[Validators.required])
    });
  }

  onTitleSubmit(){
    this.uploading = true;
    if(this.formGroup.valid){
      this.productServices.postMethod(
        (response)=>{
          this.eventEmitor.emit();
          this.uploading = false;
          console.log(response);
        },
        (error)=>{
          console.log(error);
        },
        {
          "id":this.data.id,
          "title":this.formGroup.get('title').value
        },
        "dashboard/slideradds/1"
      );
    }
    else{
      alert("Please enter a valid details");
    }
  }

  onSelectSlider(id,products){
    console.log("slider Image id",id);
    this.selectedSliderId = id;
    this.productList = products;
  }

  onDelete(sliderId,productId){
    console.log(sliderId,productId);
    this.productServices.deleteMethod(
      (response)=>{
        console.log(response);
        this.eventEmitor.emit();
      },
      (error)=>{
        console.log(error);
      },
      {
        "product_id":productId
      },
      "dashboard/addsliderproduct/"+sliderId
    );
  }
  uploadImage($event){
    if($event.target.files.length > 0)
    {

      this.slideGroup.patchValue({
        file:$event.target.files[0]
      });

    }
  }

  onSubmitSLides(){

    if(this.slideGroup.valid){
      this.submitedSlide = true;
      let formData = new FormData();
      console.log(this.slideGroup.get('title').value);
      formData.append("title",this.slideGroup.get('title').value);
      formData.append("images",this.slideGroup.get('file').value);
      formData.append("slider",this.data.id);
      console.log(formData.forEach((val,key)=>{
        console.log(val);
      }));
      this.productServices.postMethod(
        (response)=>{
          this.submitedSlide = false;
          console.log(response);
          this.eventEmitor.emit();
        },
        (error)=>{
          this.submitedSlide = false;
          console.log(error);
        },
        formData,
        "dashboard/slideraddsimages/"+this.data.id
      );


    }
    else{
      this.submitedSlide = true;
      alert("Please provide a valid information");
      this.submitedSlide = false;
    }

  }

  onDeleteSlider(id,content){
    this.open(id,content);
  }

  open(id,content) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResults = `Closed with: ${result}`;
      console.log(result);
      if(result == 'sure'){
        this.productServices.deleteMethod(
          (response)=>{
            console.log("deleted");
            this.eventEmitor.emit();
          },
          (error)=>{},
          {},
          "dashboard/slideraddsimages/"+id
        );
      }
    }, (reason) => {
      this.closeResults = `Dismissed ${this.getDismissReason(reason)}`;
      console.log(reason);

    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

}
