import { Component, OnInit } from '@angular/core';
import { FormControl , FormGroup } from '@angular/forms';
import { ProductService } from '../../../services/product/product.service';
import { imageBaseUrl } from '../../../Models/common';
import { Router } from '@angular/router';

@Component({
  selector: 'ngx-addproduct',
  templateUrl: './addproduct.component.html',
  styleUrls: ['./addproduct.component.scss']
})
export class AddproductComponent implements OnInit {

  // productName = new FormControl();
  suggestion:any = [];
  formGroup:FormGroup;
  baseurl:String;

  constructor(private productService:ProductService,private router:Router) { }

  ngOnInit() {
    this.baseurl = imageBaseUrl;
    this.formGroup = new FormGroup({
      productName: new FormControl('')
    });
    this.formGroup.get('productName').valueChanges.subscribe((data)=>{
      // console.log(data);
      if(data.toString().length > 0){
        this.productService.getMethod(
          (response)=>{
            this.suggestion = response;
          },
          (error)=>{},
          {
            "query":data
          },
          "dashboard/search"
        );
      }
      else{
        this.suggestion = [];
      }
    });
  }

  onSelect(id){
    console.log(id);
    // let path = this.router.url;
    let listOfPath = this.router.url.split("/");
    let sliderImageId = listOfPath[listOfPath.length - 1];

    this.productService.postMethod(
      (response)=>{
        alert("Product added successfully");
      },
      (error)=>{},
      {"product_id":id},
      "dashboard/addsliderproduct/"+sliderImageId
    );
  }

}
