import { Component, OnInit , Input ,Output , EventEmitter  } from '@angular/core';
import { FormGroup , FormControl , Validators,  } from '@angular/forms';
import { imageBaseUrl } from '../../../Models/common';
import {  } from '../../../Models/common';
import { ProductService } from '../../../services/product/product.service';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';


@Component({
  selector: 'ngx-productslider',
  templateUrl: './productslider.component.html',
  styleUrls: ['./productslider.component.scss']
})
export class ProductsliderComponent implements OnInit {

  @Input() data;
  @Output() eventEmitor = new EventEmitter();
  formGroup:FormGroup;
  addproductGroup:FormGroup;
  submited:boolean = false;
  baseUrl:string = '';
  loading = false;
  suggestion:any = [];


  constructor(private productService:ProductService,private router:Router) { }

  ngOnInit() {
    this.baseUrl = imageBaseUrl;
    this.formGroup = new FormGroup({

      title:new FormControl('',[Validators.required]),

    });

    this.addproductGroup = new FormGroup({
      productName: new FormControl('')
    });
    this.addproductGroup.get('productName').valueChanges.subscribe((data)=>{
      // console.log(data);
      if(data.toString().length > 0){
        this.productService.getMethod(
          (response)=>{
            this.suggestion = response;
          },
          (error)=>{},
          {
            "query":data
          },
          "dashboard/search"
        );
      }
      else{
        this.suggestion = [];
      }
    });
  }

  onSubmit(){
    console.log(">>>>");
    if(this.formGroup.valid){
      this.loading = true;
      let formData =new FormData();
      formData.append('title',this.formGroup.get('title').value);
      // formData.append('image',this.formGroup.get('file').value);
      formData.append('deals_component',this.data.id);
      this.productService.postMethod(
        (response)=>{
          console.log(response);
          this.loading = false;
          this.eventEmitor.emit();
        },
        (error)=>{},
        formData,
        "dashboard/productslider/1"
      );

    }
    else{
      alert("Please provide a valid details");
    }

  }

  uploadImage($event){
    if($event.target.files.length > 0)
    {

      this.formGroup.patchValue({
        file:$event.target.files[0]
      });

    }
  }

  onSelect(id){
    console.log(id);
    // let path = this.router.url;
    let listOfPath = this.router.url.split("/");
    let sliderImageId = listOfPath[listOfPath.length - 1];

    this.productService.postMethod(
      (response)=>{
        alert("Product added successfully");
        this.eventEmitor.emit();
      },
      (error)=>{
        alert("Something went wrong");
      },
      {"product_id":id,"image_add":this.data.data[0].id},
      "dashboard/addproductslider"
    );
  }

  onDelete(productId){

    this.productService.deleteMethod(
      (response)=>{
        console.log(response);
        this.eventEmitor.emit();
      },
      (error)=>{
        console.log(error);
      },
      {
        "product_id":productId,
        "image_add":this.data.data[0].id
      },
      "dashboard/addproductslider"
    );
  }

}
