import { Component, OnInit } from '@angular/core';
import { FormControl,FormGroup , Validators } from '@angular/forms';
import { ToasterConfig } from 'angular2-toaster';
import {
  NbComponentStatus,
  NbGlobalLogicalPosition,
  NbGlobalPhysicalPosition,
  NbGlobalPosition,
  NbToastrService,
} from '@nebular/theme';
import { ProductService } from '../../services/product/product.service';


@Component({
  selector: 'ngx-adddashboardcomponent',
  templateUrl: './adddashboardcomponent.component.html',
  styleUrls: ['./adddashboardcomponent.component.scss']
})
export class AdddashboardcomponentComponent implements OnInit {

  formGroup:FormGroup;
  uploading:boolean = false;
  submited:boolean = false;
  config: ToasterConfig;
  componentType:any = [];

  constructor(private productService:ProductService,private toastrService: NbToastrService) { }

  ngOnInit() {
    this.formGroup = new FormGroup({
      component_type:new FormControl('',[Validators.required])
    });

    this.productService.getMethod(
      (response)=>{

        this.componentType = response;

      },
      (error)=>{
        this.showToaster("OOPS !!","Something went wrong please try again ..","danger");
      },
      {},
      "dashboard/componenttype"
      );

  }

  onSubmit(){
    if(this.formGroup.valid){
      // const formData = new FormData();
      // formData.append('categories_image',this.formGroup.get('file').value);
      // formData.append('category_name',this.formGroup.get('name').value);
      let data = {"component_type":this.formGroup.get('component_type').value};
      this.uploading = true;
      this.productService.postMethod(
        (response)=>{
          this.uploading = false;
          this.showToaster("Success","The component added successfully.","primary");

        },
        (error)=>{
          console.log(error);
          this.uploading = false;
          this.showToaster("OOPS !!","Something went wrong please try again ..","danger");
        },
        data,
        "dashboard/component"
      );

    }
    else{
      this.showToaster("OOPS !!","Please Select a Component type.","danger");
    }
  }

  showToaster(title,body,status){
    const config = {
      status:status ,
      destroyByClick:false,
      duration: 2000,
      hasIcon: true,
      position:NbGlobalLogicalPosition.BOTTOM_END,
      preventDuplicates: false,
    };



    this.toastrService.show(
      body,
      title,
      config);
  }

}
