import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EditscreenComponent } from './editscreen/editscreen.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { AdddashboardcomponentComponent } from './adddashboardcomponent/adddashboardcomponent.component';
import { ComponentdetailsComponent } from './componentdetails/componentdetails.component';
import { AddproductComponent } from './components/addproduct/addproduct.component';

const routes: Routes = [
  {
    path:'',
    component:DashboardComponent
  },
  {
    path:'edit',
    component:EditscreenComponent
  },
  {
    path:'addcomponent',
    component:AdddashboardcomponentComponent
  },
  {
    path:'details/:id',
    component:ComponentdetailsComponent
  },
  {
    path:'addproduct/:type/:id',
    component: AddproductComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MobileRoutingModule { }
